.card {
    background: #fff;
    border-radius: 2px;
    height: 165px;
    margin: 1rem;
    width: 165px;
    border-radius: 25px;
    
  }
  
  img {
    display: block;
    margin-left: auto;
    margin-right: auto;
    width: 85%;
  }

  span{
    width:100px;
    margin: 2px;
    text-align: center;
    display: block;
    margin-left: auto;
    margin-right: auto;
    background: orange;
    color: white;
    font-size: 1em;
    border-radius: 10px;
    
  }

/* Style the header */
.header {
    background: rgb(104, 71, 9);
    color: white;
  }
  
  .titlescore{
    font-family: 'Lobster', cursive;
    font-size: 50px;
    text-align: center;
  }
  
  /* The sticky class is added to the header with JS when it reaches its scroll position */
  .sticky {
    position: fixed;
    top: 0;
    width: 100%
  }
  


.statusmsg{
    margin-top: 140px;

}

.status{
    font-family: 'Lobster', cursive;
    font-size: 100px;
    text-align: center;
}
